.btn {
  position: relative;
  width: 11rem;
  height: 11rem;
  background-color: rgb(0, 0, 0, 1);
  display: inline-block;
  margin: 0.6rem;
  padding: 0rem rem;
  outline: none;
  border: none;
  font-size: 12rem;
  color: white;
  font-weight: normal;
  border-radius: 2rem;
  cursor: pointer;
  left: 1.5rem;
  top: 1rem;
}

body {
  background-color: white;
}
.paraone {
  position: relative;
  background-color: burlywood;
  height: 17rem;
  margin-right: 0rem;
  top: 0rem;
  left: 0rem;
  width: 40rem;
}
.paratwo {
  position: relative;
  background-color: burlywood;
  height: 40rem;
  margin-right: 0rem;
  top: -2.1rem;
  left: 0rem;
  width: 40rem;
  border-top: 0.3rem solid black;
}

.musicBtn {
  position: relative;
  padding: 0.9rem;
  background-color: black;
  display: inline-block;
  margin: 0.2rem;
  font-size: 1rem;
  color: white;
  border-radius: 0.5rem;
  font-weight: bold;
  left: 28rem;
  top: 10rem;
}

.container {
  position: relative;
  width: 38.5rem;
  height: 60rem;
  border: 0rem solid rgba(0, 0, 0, 1);
  border-radius: 2rem;
  padding: 0rem;
  box-sizing: border-box;
  top: 0rem;
  left: 0rem;
  right: 0rem;
  margin: auto;
}
.heading {
  font-size: 2.5rem;
  font-weight: bold;
  color: black;
  text-align: center;
  top: 0rem;
}
